/* --- SCSS Variables --- */
// Color Palette
$primary-color: #00385E;
$secondary-color: #DDEBF3;
$hover-color: #A4C8E1;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff;
$text-color-primary: #212529;
$text-color-secondary: #495057;
$text-color-muted: #6c757d;
$border-color: #dee2e6;

// Layout & Spacing
$border-radius: 8px;
$base-padding: 1.5rem;
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Main Content Area --- */
.main-content {
    flex-grow: 1;
    padding: $grid-gap;
    overflow-y: auto;
}

/* --- Inventory Page Container --- */
.inventory-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid rgba($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: darken($text-color-secondary, 20%);
    }

    .header-actions {
        display: flex;
        gap: 0.5rem;
    }
}

/* --- Inventory Controls --- */
.inventory-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding: $base-margin;
    background-color: lighten($secondary-color, 5%);
    border-radius: $border-radius;
    flex-wrap: wrap;
    gap: $base-margin;
}

.view-toggle {
    display: flex;
    gap: 0.5rem;

    .toggle-link {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.5rem 1rem;
        text-decoration: none;
        color: $text-color-secondary;
        border-radius: $border-radius;
        transition: all 0.2s ease-in-out;
        font-weight: 500;

        &:hover {
            background-color: $hover-color;
            color: $text-color-primary;
        }

        &.active {
            background-color: $primary-color;
            color: white;
        }

        .material-symbols-outlined {
            font-size: 1.1rem;
        }
    }
}

.filter-form {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    flex-wrap: wrap;

    select, input[type="text"] {
        padding: 0.5rem 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        background-color: $container-background-color;
        min-width: 120px;
        font-size: 0.9rem;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 2px rgba($primary-color, 0.2);
        }
    }

    .checkbox-label {
        display: flex;
        align-items: center;
        gap: 0.25rem;
        font-size: 0.9rem;
        color: $text-color-secondary;
        cursor: pointer;
    }
}

/* --- Inventory Summary View --- */
.inventory-summary {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: $grid-gap;
}

.summary-card {
    background-color: $container-background-color;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: $base-padding;
    box-shadow: $base-shadow;
    transition: all 0.2s ease-in-out;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
    }

    &.low-stock {
        border-left: 4px solid $warning-color;
        background-color: lighten($warning-color, 45%);
    }
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: $base-margin;

    h3 {
        font-size: 1.1rem;
        font-weight: 600;
        color: $text-color-primary;
        margin: 0;
        margin-bottom: 0.25rem;
    }

    .sku {
        font-size: 0.85rem;
        color: $text-color-secondary;
        background-color: lighten($secondary-color, 3%);
        padding: 0.2rem 0.5rem;
        border-radius: calc($border-radius / 2);
        font-family: monospace;
    }
}

.card-content {
    margin-bottom: $base-margin;
}

.stock-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $base-margin;
}

.stock-number {
    text-align: center;

    .stock-value {
        display: block;
        font-size: 2rem;
        font-weight: 700;
        color: $primary-color;
        line-height: 1;
    }

    .stock-label {
        font-size: 0.8rem;
        color: $text-color-secondary;
        text-transform: uppercase;
        letter-spacing: 0.5px;
    }
}

.stock-details {
    .detail {
        display: flex;
        align-items: center;
        gap: 0.25rem;
        margin-bottom: 0.25rem;
        font-size: 0.85rem;
        color: $text-color-secondary;

        .material-symbols-outlined {
            font-size: 1rem;
        }
    }
}

.low-stock-warning {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem;
    background-color: lighten($warning-color, 35%);
    border: 1px solid lighten($warning-color, 20%);
    border-radius: $border-radius;
    color: darken($warning-color, 30%);
    font-size: 0.85rem;
    font-weight: 500;

    .material-symbols-outlined {
        font-size: 1rem;
    }
}

.card-actions {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;

    .btn-sm {
        padding: 0.4rem 0.8rem;
        font-size: 0.8rem;
    }
}

/* --- Inventory Table View --- */
.inventory-table-container {
    overflow-x: auto;
    border-radius: $border-radius;
    border: 1px solid $border-color;
}

.inventory-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.9rem;

    th, td {
        padding: 0.75rem;
        text-align: left;
        border-bottom: 1px solid $border-color;
        vertical-align: middle;
    }

    thead th {
        background-color: lighten($secondary-color, 3%);
        font-weight: 600;
        color: $text-color-primary;
        border-bottom: 2px solid darken($border-color, 10%);
        position: sticky;
        top: 0;
        z-index: 10;
    }

    tbody tr {
        transition: background-color 0.15s ease-in-out;

        &:hover {
            background-color: lighten($secondary-color, 8%);
        }
    }

    .product-info, .location-info {
        display: flex;
        flex-direction: column;
        gap: 0.2rem;

        strong {
            font-weight: 600;
            color: $text-color-primary;
        }

        span {
            font-size: 0.8rem;
            color: $text-color-secondary;
        }
    }

    .quantity {
        font-weight: 600;
        text-align: center;
        color: $text-color-primary;
    }

    .batch-info {
        display: flex;
        flex-direction: column;
        gap: 0.1rem;

        span {
            font-size: 0.8rem;
            color: $text-color-secondary;
            font-family: monospace;
        }
    }

    .expiry-date {
        &.expired {
            color: $danger-color;
            font-weight: 600;
        }

        &.expiring-soon {
            color: $warning-color;
            font-weight: 600;
        }

        small {
            display: block;
            font-size: 0.7rem;
            margin-top: 0.1rem;
        }
    }

    .text-muted {
        color: $text-color-muted;
        font-style: italic;
    }

    .text-center {
        text-align: center;
    }

    .text-right {
        text-align: right;
    }
}

.table-actions {
    display: flex;
    gap: 0.25rem;
    justify-content: center;

    .btn-sm {
        padding: 0.3rem 0.6rem;
        font-size: 0.8rem;

        .material-symbols-outlined {
            font-size: 1rem;
        }
    }
}

/* --- Low Stock Alerts --- */
.low-stock-alerts {
    display: flex;
    flex-direction: column;
    gap: $base-margin;
}

.alert-card {
    display: flex;
    align-items: center;
    gap: $base-margin;
    padding: $base-margin;
    background-color: lighten($warning-color, 45%);
    border: 1px solid lighten($warning-color, 25%);
    border-left: 4px solid $warning-color;
    border-radius: $border-radius;
    transition: all 0.2s ease-in-out;

    &:hover {
        box-shadow: 0 4px 12px rgba($warning-color, 0.2);
    }
}

.alert-icon {
    .material-symbols-outlined {
        font-size: 2rem;
        color: darken($warning-color, 15%);
    }
}

.alert-content {
    flex-grow: 1;

    h3 {
        margin: 0 0 0.25rem 0;
        font-size: 1.1rem;
        font-weight: 600;
        color: $text-color-primary;
    }

    .alert-details {
        display: flex;
        gap: $base-margin;
        align-items: center;
        flex-wrap: wrap;

        .sku {
            font-family: monospace;
            background-color: lighten($warning-color, 35%);
            padding: 0.2rem 0.5rem;
            border-radius: calc($border-radius / 2);
            font-size: 0.8rem;
        }

        .stock-info {
            font-size: 0.9rem;
            color: $text-color-secondary;

            strong {
                color: $text-color-primary;
            }
        }
    }
}

.alert-actions {
    .btn {
        padding: 0.5rem 1rem;
        font-size: 0.9rem;
    }
}

/* --- Button Styles --- */
.btn {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.6rem 1.2rem;
    font-size: 0.95rem;
    font-weight: 500;
    border-radius: $border-radius;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    white-space: nowrap;

    .material-symbols-outlined {
        font-size: 1.25rem;
    }

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    }
}

.btn-primary {
    background-color: $primary-color;
    color: #fff;
    &:hover { background-color: darken($primary-color, 8%); }
}

.btn-secondary {
    background-color: $text-color-secondary;
    color: #fff;
    &:hover { background-color: darken($text-color-secondary, 8%); }
}

.btn-success {
    background-color: $success-color;
    color: #fff;
    &:hover { background-color: darken($success-color, 8%); }
}

.btn-warning {
    background-color: $warning-color;
    color: #fff;
    &:hover { background-color: darken($warning-color, 8%); }
}

.btn-danger {
    background-color: $danger-color;
    color: #fff;
    &:hover { background-color: darken($danger-color, 8%); }
}

/* --- Modal Styles --- */
.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.2s ease-in-out;
}

.modal-content {
    background-color: $container-background-color;
    margin: 3% auto;
    padding: 0;
    border-radius: $border-radius;
    width: 90%;
    max-width: 600px;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    animation: slideIn 0.3s ease-in-out;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $base-padding;
    border-bottom: 1px solid $border-color;
    position: sticky;
    top: 0;
    background-color: $container-background-color;
    z-index: 10;

    .modal-title {
        margin: 0;
        font-size: 1.25rem;
        font-weight: 600;
        color: $text-color-primary;
    }

    .close {
        background: none;
        border: none;
        font-size: 1.5rem;
        cursor: pointer;
        color: $text-color-secondary;
        padding: 0;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.2s ease-in-out;

        &:hover {
            background-color: lighten($danger-color, 40%);
            color: $danger-color;
        }
    }
}

/* --- Form Styles --- */
.form-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: $base-margin;
}

.form-group {
    margin-bottom: $base-margin;

    .form-label {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: 500;
        color: $text-color-primary;
    }

    .form-input, .form-textarea, select {
        width: 100%;
        padding: 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        font-size: 0.95rem;
        transition: all 0.2s ease-in-out;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 3px rgba($primary-color, 0.1);
        }

        &::placeholder {
            color: lighten($text-color-secondary, 20%);
        }
    }
}

.form-help {
    margin-top: 0.5rem;
    padding: 0.5rem;
    background-color: lighten($info-color, 40%);
    border-left: 3px solid $info-color;
    border-radius: $border-radius;
    font-size: 0.85rem;
    color: darken($info-color, 20%);

    p {
        margin: 0;
    }

    strong {
        color: darken($info-color, 30%);
    }
}

.form-actions {
    display: flex;
    gap: $base-margin;
    justify-content: flex-end;
    padding: $base-padding;
    border-top: 1px solid $border-color;
    background-color: lighten($secondary-color, 8%);
    margin: 0;
    position: sticky;
    bottom: 0;
}

.info-section {
    padding: $base-padding;
    background-color: lighten($info-color, 45%);
    border-radius: $border-radius;
    margin-bottom: $base-margin;

    h4 {
        margin: 0 0 0.5rem 0;
        color: $text-color-primary;
        font-size: 1rem;
    }

    p {
        margin: 0.25rem 0;
        font-size: 0.9rem;
        color: $text-color-secondary;

        strong {
            color: $text-color-primary;
        }
    }
}

/* --- Message Styles --- */
.message {
    padding: $base-margin;
    margin-bottom: $grid-gap;
    border-radius: $border-radius;
    border: 1px solid;
    font-weight: 500;

    &.success {
        background-color: lighten($success-color, 40%);
        border-color: darken(lighten($success-color, 40%), 10%);
        color: darken($success-color, 10%);
    }

    &.error {
        background-color: lighten($danger-color, 30%);
        border-color: darken(lighten($danger-color, 30%), 10%);
        color: darken($danger-color, 10%);
    }
}

/* --- Empty State --- */
.empty-state {
    text-align: center;
    padding: 3rem;
    color: $text-color-secondary;

    .material-symbols-outlined {
        font-size: 4rem;
        margin-bottom: $base-margin;
        opacity: 0.6;
    }

    h3 {
        margin-bottom: 0.5rem;
        color: $text-color-primary;
    }

    p {
        font-size: 0.95rem;
    }
}

/* --- Animations --- */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideIn {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* --- Responsive Design --- */
@media (max-width: 768px) {
    .inventory-summary {
        grid-template-columns: 1fr;
    }

    .inventory-controls {
        flex-direction: column;
        align-items: stretch;
    }

    .view-toggle {
        justify-content: center;
    }

    .filter-form {
        flex-direction: column;
        align-items: stretch;

        select, input[type="text"] {
            min-width: auto;
        }
    }

    .page-header {
        flex-direction: column;
        gap: $base-margin;
        align-items: stretch;

        .header-actions {
            justify-content: center;
        }
    }

    .form-grid {
        grid-template-columns: 1fr;
    }

    .stock-info {
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: $base-margin;
    }

    .alert-card {
        flex-direction: column;
        text-align: center;
        gap: 0.75rem;
    }

    .inventory-table-container {
        font-size: 0.8rem;
    }
}